<template>
  <div class="settings-container">
    <el-card>
      <template #header>
        <div class="card-header">
          <span>个人设置</span>
        </div>
      </template>
      
      <el-tabs v-model="activeTab">
        <el-tab-pane label="个人信息" name="profile">
          <el-form 
            :model="profileForm" 
            label-width="100px" 
            :rules="profileRules" 
            ref="profileFormRef"
            class="settings-form"
          >
            <el-form-item label="用户名" prop="username">
              <el-input v-model="profileForm.username" placeholder="请输入用户名"></el-input>
            </el-form-item>
            
            <el-form-item label="邮箱" prop="email">
              <el-input v-model="profileForm.email" placeholder="请输入邮箱" type="email"></el-input>
            </el-form-item>
            
            <el-form-item>
              <el-button type="primary" @click="updateProfile" :loading="profileSubmitting">保存</el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        
        <el-tab-pane label="修改密码" name="password">
          <el-form 
            :model="passwordForm" 
            label-width="120px" 
            :rules="passwordRules" 
            ref="passwordFormRef"
            class="settings-form"
          >
            <el-form-item label="当前密码" prop="currentPassword">
              <el-input 
                v-model="passwordForm.currentPassword" 
                placeholder="请输入当前密码" 
                type="password"
                show-password
              ></el-input>
            </el-form-item>
            
            <el-form-item label="新密码" prop="newPassword">
              <el-input 
                v-model="passwordForm.newPassword" 
                placeholder="请输入新密码" 
                type="password"
                show-password
              ></el-input>
            </el-form-item>
            
            <el-form-item label="确认新密码" prop="confirmPassword">
              <el-input 
                v-model="passwordForm.confirmPassword" 
                placeholder="请再次输入新密码" 
                type="password"
                show-password
              ></el-input>
            </el-form-item>
            
            <el-form-item>
              <el-button type="primary" @click="updatePassword" :loading="passwordSubmitting">保存</el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        
        <el-tab-pane label="API设置" name="api">
          <el-form 
            :model="apiForm" 
            label-width="150px" 
            ref="apiFormRef"
            class="settings-form"
          >
            <el-divider content-position="left">Coze API 配置</el-divider>
            
            <el-form-item label="Coze API令牌" prop="cozeApiToken">
              <el-input 
                v-model="apiForm.cozeApiToken" 
                placeholder="请输入Coze API访问令牌（PAT）" 
                type="password"
                show-password
                clearable
              ></el-input>
              <div class="form-tip">个人访问令牌，以pat_开头的字符串</div>
            </el-form-item>
            
            <el-form-item label="工作流ID" prop="cozeWorkflowId">
              <el-input 
                v-model="apiForm.cozeWorkflowId" 
                placeholder="请输入Coze工作流ID" 
                clearable
              ></el-input>
              <div class="form-tip">已发布的生成商品描述工作流ID</div>
            </el-form-item>
            
            <el-form-item label="智能体ID" prop="cozeBotId">
              <el-input 
                v-model="apiForm.cozeBotId" 
                placeholder="请输入智能体ID（可选）" 
                clearable
              ></el-input>
              <div class="form-tip">可选，如果工作流需要关联智能体，则需填写</div>
            </el-form-item>
            
            <el-form-item>
              <el-button type="primary" @click="updateApiSettings" :loading="apiSubmitting">保存</el-button>
              <el-button @click="testCozeApi" :loading="apiTesting">测试连接</el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        
        <el-tab-pane label="默认参数设置" name="defaults">
          <el-form 
            :model="defaultsForm" 
            label-width="150px" 
            ref="defaultsFormRef"
            class="settings-form"
          >
            <el-divider content-position="left">闲鱼商品默认参数</el-divider>
            
            <el-form-item label="商品业务类型" prop="item_biz_type">
              <el-input-number v-model="defaultsForm.item_biz_type" :min="1" :step="1"></el-input-number>
              <div class="form-tip">默认值: 2</div>
            </el-form-item>
            
            <el-form-item label="特殊业务类型" prop="sp_biz_type">
              <el-input-number v-model="defaultsForm.sp_biz_type" :min="1" :step="1"></el-input-number>
              <div class="form-tip">默认值: 21</div>
            </el-form-item>
            
            <el-form-item label="商品类目ID" prop="channel_cat_id">
              <el-input v-model="defaultsForm.channel_cat_id" placeholder="请输入商品类目ID"></el-input>
              <div class="form-tip">默认值: 9296cbae4daae3c4267e0a01017d9f44</div>
            </el-form-item>
            
            <el-form-item label="运费" prop="express_fee">
              <el-input-number v-model="defaultsForm.express_fee" :min="0" :precision="2"></el-input-number>
              <div class="form-tip">默认值: 0.00</div>
            </el-form-item>
            
            <el-form-item label="库存" prop="stock">
              <el-input-number v-model="defaultsForm.stock" :min="1" :step="1"></el-input-number>
              <div class="form-tip">默认值: 1</div>
            </el-form-item>
            
            <el-form-item label="新旧程度" prop="stuff_status">
              <el-select v-model="defaultsForm.stuff_status" placeholder="请选择新旧程度">
                <el-option label="全新" :value="0"></el-option>
                <el-option label="9成新" :value="1"></el-option>
                <el-option label="8成新" :value="2"></el-option>
                <el-option label="7成新" :value="3"></el-option>
                <el-option label="6成新" :value="4"></el-option>
                <el-option label="5成新及以下" :value="5"></el-option>
              </el-select>
              <div class="form-tip">默认值: 0 (全新)</div>
            </el-form-item>
            
            <el-form-item label="规格类型" prop="spec_type">
              <el-input-number v-model="defaultsForm.spec_type" :min="1" :max="3" :step="1"></el-input-number>
              <div class="form-tip">默认值: 1</div>
            </el-form-item>
            
            <el-divider content-position="left">发布计划默认参数</el-divider>
            
            <el-form-item label="默认发布数量" prop="default_publish_count">
              <el-input-number v-model="defaultsForm.default_publish_count" :min="1" :max="100" :step="1"></el-input-number>
              <div class="form-tip">默认值: 5</div>
            </el-form-item>
            
            <el-form-item label="默认图片数量" prop="default_image_count">
              <el-input-number v-model="defaultsForm.default_image_count" :min="1" :max="9" :step="1"></el-input-number>
              <div class="form-tip">默认值: 3</div>
            </el-form-item>
            
            <el-form-item label="默认价格" prop="default_price">
              <el-input-number v-model="defaultsForm.default_price" :min="0" :precision="2"></el-input-number>
              <div class="form-tip">默认值: 50.00</div>
            </el-form-item>
            
            <el-form-item>
              <el-button type="primary" @click="updateDefaults" :loading="defaultsSubmitting">保存</el-button>
              <el-button @click="resetDefaults">恢复默认值</el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
import { ref, reactive, onMounted } from 'vue'
import { ElMessage } from 'element-plus'
import axios from 'axios'
import { useRoute } from 'vue-router'

export default {
  name: 'SettingsPage',
  setup() {
    const route = useRoute();
    const activeTab = ref('profile')
    
    // 个人信息相关
    const profileFormRef = ref(null)
    const profileSubmitting = ref(false)
    const profileForm = reactive({
      username: '',
      email: ''
    })
    
    const profileRules = {
      username: [
        { required: true, message: '请输入用户名', trigger: 'blur' },
        { min: 3, max: 20, message: '用户名长度应为3-20个字符', trigger: 'blur' }
      ],
      email: [
        { required: true, message: '请输入邮箱', trigger: 'blur' },
        { type: 'email', message: '请输入正确的邮箱格式', trigger: 'blur' }
      ]
    }
    
    // 密码相关
    const passwordFormRef = ref(null)
    const passwordSubmitting = ref(false)
    const passwordForm = reactive({
      currentPassword: '',
      newPassword: '',
      confirmPassword: ''
    })
    
    const validateConfirmPassword = (rule, value, callback) => {
      if (value !== passwordForm.newPassword) {
        callback(new Error('两次输入的密码不一致'))
      } else {
        callback()
      }
    }
    
    const passwordRules = {
      currentPassword: [
        { required: true, message: '请输入当前密码', trigger: 'blur' }
      ],
      newPassword: [
        { required: true, message: '请输入新密码', trigger: 'blur' },
        { min: 6, message: '密码长度至少为6个字符', trigger: 'blur' }
      ],
      confirmPassword: [
        { required: true, message: '请再次输入新密码', trigger: 'blur' },
        { validator: validateConfirmPassword, trigger: 'blur' }
      ]
    }
    
    // API设置相关
    const apiFormRef = ref(null)
    const apiSubmitting = ref(false)
    const apiTesting = ref(false)
    const apiForm = reactive({
      cozeApiToken: '',
      cozeWorkflowId: '',
      cozeBotId: ''
    })
    
    // 默认参数相关
    const defaultsFormRef = ref(null)
    const defaultsSubmitting = ref(false)
    const defaultsForm = reactive({
      // 闲鱼商品默认参数
      item_biz_type: 2,
      sp_biz_type: 21,
      channel_cat_id: '9296cbae4daae3c4267e0a01017d9f44',
      express_fee: 0,
      stock: 1,
      stuff_status: 0,
      spec_type: 1,
      
      // 发布计划默认参数
      default_publish_count: 5,
      default_image_count: 3,
      default_price: 50.00
    })
    
    // 初始默认值，用于重置
    const initialDefaults = { ...defaultsForm }
    
    const fetchProfile = async () => {
      try {
        // 将在后续连接实际API
        // 目前使用模拟数据
        await new Promise(resolve => setTimeout(resolve, 500))
        
        profileForm.username = 'testuser'
        profileForm.email = 'test@example.com'
      } catch (error) {
        console.error('获取个人信息失败:', error)
        ElMessage.error('获取个人信息失败')
      }
    }
    
    const fetchApiSettings = () => {
      try {
        // 从本地存储获取API设置
        const savedApiSettings = localStorage.getItem('apiSettings')
        if (savedApiSettings) {
          const parsedSettings = JSON.parse(savedApiSettings)
          Object.assign(apiForm, parsedSettings)
        }
      } catch (error) {
        console.error('获取API设置失败:', error)
      }
    }
    
    const fetchDefaults = async () => {
      try {
        // 将在后续连接实际API
        // 目前使用模拟数据
        await new Promise(resolve => setTimeout(resolve, 500))
        
        // 使用默认值，如果有本地存储则使用本地存储的值
        const savedDefaults = localStorage.getItem('defaultSettings')
        if (savedDefaults) {
          const parsedDefaults = JSON.parse(savedDefaults)
          Object.assign(defaultsForm, parsedDefaults)
        }
      } catch (error) {
        console.error('获取默认参数失败:', error)
        ElMessage.error('获取默认参数失败')
      }
    }
    
    const updateProfile = async () => {
      if (!profileFormRef.value) return
      
      await profileFormRef.value.validate(async (valid) => {
        if (!valid) return
        
        profileSubmitting.value = true
        try {
          // 这里将在后续连接实际API
          // 目前只是模拟成功
          await new Promise(resolve => setTimeout(resolve, 1000))
          
          ElMessage.success('个人信息更新成功')
        } catch (error) {
          console.error('更新个人信息失败:', error)
          ElMessage.error('更新个人信息失败，请重试')
        } finally {
          profileSubmitting.value = false
        }
      })
    }
    
    const updatePassword = async () => {
      if (!passwordFormRef.value) return
      
      await passwordFormRef.value.validate(async (valid) => {
        if (!valid) return
        
        passwordSubmitting.value = true
        try {
          // 这里将在后续连接实际API
          // 目前只是模拟成功
          await new Promise(resolve => setTimeout(resolve, 1000))
          
          ElMessage.success('密码修改成功')
          
          // 清空表单
          passwordForm.currentPassword = ''
          passwordForm.newPassword = ''
          passwordForm.confirmPassword = ''
          passwordFormRef.value.resetFields()
        } catch (error) {
          console.error('修改密码失败:', error)
          ElMessage.error('修改密码失败，请重试')
        } finally {
          passwordSubmitting.value = false
        }
      })
    }
    
    const updateApiSettings = async () => {
      apiSubmitting.value = true
      try {
        // 保存到本地存储
        localStorage.setItem('apiSettings', JSON.stringify(apiForm))
        
        ElMessage.success('API设置保存成功')
      } catch (error) {
        console.error('保存API设置失败:', error)
        ElMessage.error('保存API设置失败，请重试')
      } finally {
        apiSubmitting.value = false
      }
    }
    
    const testCozeApi = async () => {
      if (!apiForm.cozeApiToken || !apiForm.cozeWorkflowId) {
        ElMessage.warning('请先填写API令牌和工作流ID')
        return
      }
      
      apiTesting.value = true
      try {
        const response = await axios.post('/api/test-coze-api', {
          accessToken: apiForm.cozeApiToken,
          workflowId: apiForm.cozeWorkflowId,
          botId: apiForm.cozeBotId || undefined
        })
        
        if (response.data.success) {
          ElMessage.success('API连接测试成功')
        } else {
          ElMessage.error(`API连接失败: ${response.data.message}`)
        }
      } catch (error) {
        console.error('测试API连接失败:', error)
        ElMessage.error('API连接测试失败，请检查配置')
      } finally {
        apiTesting.value = false
      }
    }
    
    const updateDefaults = async () => {
      defaultsSubmitting.value = true
      try {
        // 这里将在后续连接实际API
        // 目前只是模拟成功
        await new Promise(resolve => setTimeout(resolve, 1000))
        
        // 保存到本地存储
        localStorage.setItem('defaultSettings', JSON.stringify(defaultsForm))
        
        ElMessage.success('默认参数更新成功')
      } catch (error) {
        console.error('更新默认参数失败:', error)
        ElMessage.error('更新默认参数失败，请重试')
      } finally {
        defaultsSubmitting.value = false
      }
    }
    
    const resetDefaults = () => {
      Object.assign(defaultsForm, initialDefaults)
      ElMessage.success('已恢复默认值')
    }
    
    onMounted(() => {
      fetchProfile()
      fetchApiSettings()
      fetchDefaults()
      
      // 处理URL参数，自动切换到指定标签
      if (route.query.tab && ['profile', 'password', 'api', 'defaults'].includes(route.query.tab)) {
        activeTab.value = route.query.tab;
      }
    })
    
    return {
      activeTab,
      profileFormRef,
      profileSubmitting,
      profileForm,
      profileRules,
      passwordFormRef,
      passwordSubmitting,
      passwordForm,
      passwordRules,
      apiFormRef,
      apiSubmitting,
      apiTesting,
      apiForm,
      defaultsFormRef,
      defaultsSubmitting,
      defaultsForm,
      updateProfile,
      updatePassword,
      updateApiSettings,
      testCozeApi,
      updateDefaults,
      resetDefaults
    }
  }
}
</script>

<style scoped>
.settings-container {
  padding: 20px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.settings-form {
  max-width: 600px;
  margin: 20px auto;
}

.form-tip {
  font-size: 12px;
  color: #909399;
  margin-top: 5px;
}
</style> 